:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.item {
  text-transform: uppercase;
  position: relative;
}

.item:not(:first-of-type) {
  margin-left: 20px;
}

.link {
  color: #000;
  padding: 22.5px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: block;
}

.link:hover, .is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin-top: 19px;
  display: block;
  position: absolute;
}
